<template>
    <div>
        <el-card>
            <el-descriptions class="margin-top" title="作业信息" :column="2" :size="size" border>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <user />
                            </el-icon>
                            题目
                        </div>
                    </template>
                    {{ work.title_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <iphone />
                            </el-icon>
                            出题老师
                        </div>
                    </template>
                    {{ work.teacher_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <location />
                            </el-icon>
                            课程
                        </div>
                    </template>
                    {{ work.course_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            完成情况
                        </div>
                    </template>
                    {{ work.completion_degree }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            开始时间
                        </div>
                    </template>
                    {{ work.start_time }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            结束时间
                        </div>
                    </template>
                    {{ work.end_time }}
                </el-descriptions-item>
            </el-descriptions>
            <el-descriptions direction="vertical" :column="1" :size="size" border>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            内容
                        </div>
                    </template>
                    <pre>{{ work.title_content }}</pre>
                </el-descriptions-item>
            </el-descriptions>
        </el-card>
        <el-card class="header">
            <el-form :inline="true" class="headerForm" :model="searchHeaderForm">
                <el-form-item><el-input clearable v-model="searchHeaderForm.find" placeholder="请输入名称"></el-input>
                </el-form-item>
                <el-button type="primary" @click="getDatas">查询</el-button>
                <el-button class="searchBtn" type="primary" @click="submitImport">导入作业</el-button>
            </el-form>
        </el-card>
        <el-card>
            <el-table :data="tableData" border style="width: 100%"
                :header-cell-style="{ background: '#F3F4F7', color: '#555' }">
                <el-table-column prop="name" label="学生名称" min-width="200">
                </el-table-column>
                <el-table-column prop="class_name" label="班级" min-width="200">
                </el-table-column>
                <el-table-column prop="school_student_id" label="学号" min-width="200">
                </el-table-column>
                <el-table-column prop="isUpdate" label="是否上传作业" min-width="200">
                    <template #default="scope">
                        <el-tag :type="scope.row.isUpdate === false ? 'error' : 'success'" disable-transitions>
                            {{ scope.row.isUpdate }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column align="center" fixed="right" label="操作" width="200">
                    <template #default="scope">
                        <el-button type="primary" @click="showDetails(scope.row)"
                            :disabled="scope.row.isUpdate == false ? true : false">详情</el-button> </template>
                </el-table-column>
            </el-table>
            <div v-if="tableData.length !== 0" style="display: flex; flex-direction: row-reverse; margin-top: 10px">
                <el-pagination :current-page="currentPage" :page-size="pageSize" :total="total"
                    :page-sizes="[10, 20, 50, 100]" background layout="total, sizes, prev, pager, next, jumper"
                    @size-change="handleSizeChange" @current-change="handleCurrentChange" />
            </div>
        </el-card>
        <el-dialog :modal-append-to-body="false" v-model="operaDialog" :title="dialogTitle" @close="resetForm">
            <el-descriptions class="margin-top" title="回答信息" :column="2" :size="size" border v-if="!isUpload">
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <iphone />
                            </el-icon>
                            上传方式
                        </div>
                    </template>
                    {{ answer.content_msg.work_type }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            作业查重情况
                        </div>
                    </template>
                    <el-tag size="small" v-if="answer.hamming_distance == -2">待查重</el-tag>
                    <el-tag type="success" size="small" v-if="answer.hamming_distance == -1">没有抄袭</el-tag>
                    <el-tag type="danger" size="small" v-if="answer.hamming_distance > -1 && answer.hamming_distance < 4">
                        海明距离：{{ answer.hamming_distance }}，认定为抄袭</el-tag>
                    <el-tag type="warning" size="small" v-if="answer.hamming_distance > 3">
                        海明距离：{{ answer.hamming_distance }}，可能为抄袭</el-tag>
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <location />
                            </el-icon>
                            首次上传时间
                        </div>
                    </template>
                    {{ answer.create_time }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            修改时间
                        </div>
                    </template>
                    {{ answer.update_time }}
                </el-descriptions-item>

            </el-descriptions>

            <el-descriptions direction="vertical" :column="1" :size="size" border v-if="isUploadFile">
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            文件
                        </div>
                    </template>
                    <el-icon :style="iconStyle">
                        <Document />
                    </el-icon>
                    {{ answer.content_msg.file_name }}
                    <div style="float: right">
                        <el-button type="primary" @click="downloadFile()">下载</el-button>
                    </div>
                </el-descriptions-item>
            </el-descriptions>

            <el-descriptions direction="vertical" :column="1" :size="size" border v-if="isUploadPic">
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            图片
                        </div>
                    </template>
                    <div class="demo-image__preview">
                        <el-image v-for="url in answer.content_msg.pic_url"
                            style="width: 100px; height: 100px; margin-left: 20px" :src="url" :preview-src-list="[url]"
                            :initial-index="4" fit="cover" />
                    </div>
                </el-descriptions-item>
            </el-descriptions>

            <!-- <el-descriptions direction="vertical" :column="1" :size="size" border v-if="!isUpload">
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            内容
                        </div>
                    </template>
                    <pre>{{ answer.content_msg.content }}</pre>
                </el-descriptions-item>
            </el-descriptions> -->


        </el-dialog>

        <!-- 上传文件导入学生 -->
        <el-dialog :modal-append-to-body="false" v-model="studentDialog" :title="dialogTitle" @close="resetFormRole">
            <el-upload drag v-model:file-list="fileList" name="file"
                action="http://127.0.0.1:11002/work/work/answer/upload/zip" :headers="headers"
                :on-success="uploadfileSuccess">
                <el-icon class="el-icon--upload"><upload-filled /></el-icon>
                <div class="el-upload__text">
                    上传答题结果zip
                </div>
                <template #tip>
                    <div>
                        <el-link type="primary"
                            href="http://101.200.87.232:19000/example/importStudent.xlsx">模板文件下载</el-link>
                    </div>
                </template>
            </el-upload>

        </el-dialog>
    </div>
</template>
<script>
import moment from "moment";
import {
    work_find,
    work_show_student,
    work_answer_details_by_teacher
} from "@/api/teacher/api.js";
import axios from "axios";
// eslint-disable-next-line no-unused-vars
export default {
    data() {
        return {

            work: {},
            tableData: [],

            headers: {
                token: sessionStorage.getItem('token'),
                user_id: sessionStorage.getItem('user_id'),
                work_id: this.$route.query.work_id
            },
            studentDialog: false,

            isMycourse: false,//是不是自己的课程
            operaDialog: false,
            pageSize: 10, // 分页的页面大小
            total: 0, // 分页的数据总数
            currentPage: 1, // 分页的当前页
            //查询
            searchHeaderForm: {
                find: '',
            },
            answer: {
                hamming_distance: "",
                hash_64: "",
                create_time: "",
                update_time: "",
                content_msg: {
                    content: "",
                    file_name: "",
                    file_url: "",
                    pic_url: [],
                    work_type: 0
                }
            },
            fileList: [
            ],
            isUploadPic: false,
            isUploadFile: false,
        }
    },
    created() {
        this.getUserMessage();
    },
    methods: {
        async getUserMessage() {
            var work_id = this.$route.query.work_id
            work_find({ work_id: work_id }).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.work = res.data.data.data[0]
                    this.getDatas()
                    // this.$router.push('/manager').catch(() => { })
                }
            })
        },
        async getDatas() {
            const params = {
                work_id: this.$route.query.work_id,
                page: this.currentPage,
                page_size: this.pageSize,
                search: this.searchHeaderForm.find,
            }
            const res = await work_show_student(params)
            if (res.data.code === 200) {
                if (res.data.data) {
                    this.total = res.data.data.count
                    if (res.data.data.data == null) {
                        this.tableData = []
                    } else {
                        this.tableData = res.data.data.data
                    }
                } else {
                    this.total = res.data.data.count
                    this.tableData = []
                }
            }
        },

        uploadWorkFile() {
        },
        showDetails(data) {
            this.operaDialog = true
            var payload = {
                student_id: data._id,
                work_id: this.work._id
            }
            work_answer_details_by_teacher(payload).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    var resan = res.data.data
                    if (resan.content_msg.work_type == 1) {
                        resan.content_msg.work_type = "上传文字"
                        this.isUploadFile = false
                        this.isUploadPic = false
                    }
                    if (resan.content_msg.work_type == 2) {
                        resan.content_msg.work_type = "上传文件"
                        this.isUploadFile = true
                        this.isUploadPic = false
                    }
                    if (resan.content_msg.work_type == 3) {
                        resan.content_msg.work_type = "上传图片"
                        this.isUploadFile = false
                        this.isUploadPic = true
                    }
                    this.answer = resan

                    // this.$router.push('/manager').catch(() => { })
                }
            })
        },
        downloadFile() {
            var url = this.answer.content_msg.file_url
            var name = this.answer.content_msg.file_name
            // console.log(this.answer.content_msg.file_url);
            axios.get(this.answer.content_msg.file_url)

            axios({
                method: 'get',
                url: url,
                responseType: 'blob',
            }).then(function (response) {
                const link = document.createElement('a')
                let blob = new Blob([response.data], { type: response.data.type })
                let url = URL.createObjectURL(blob)
                link.href = url
                link.download = name
                link.click()
            })

        },
        submitImport() {
            this.studentDialog = true
        },
        // resetForm(this.answer.content_msg.file_url) {
        //     this.answer = {}
        // },
    },
};
</script>
  
<style lang="less" scoped>
.header {
    margin-top: 20px;
    margin-bottom: 20px;

    .searchBtn {
        float: right;
    }

    .headerForm {
        padding: 0 !important;

        .el-form-item {
            margin-bottom: 0px;
        }
    }
}

.up {
    margin-top: 20px;
}

.cell-item {
    display: flex;
    align-items: center;
}
</style>
  